@use "../../styles/fontSizes"
@use "../../styles/functions"

.footer
    margin-top: functions.toRem(50)
    &__content
        font-size: fontSizes.$middle
        padding: functions.toRem(25) 0
        display: flex
        align-items: center
        justify-content: space-between
        height: functions.toRem(100)
    &__logo
        align-items: center
        display: flex

    &__image
        margin-right: functions.toRem(10)
        width: functions.toRem(30)

    &__accent
        font-weight: bold
        color: #7176F7
    &__copyright
        text-align: right
        font-size: fontSizes.$default

@media (max-width: 1000px)
    .footer
        &__content
            height: functions.toRem(50)
            font-size: fontSizes.$mobileMiddle
        &__copyright
            font-size: fontSizes.$mobileMiddle
        &__image
            width: functions.toRem(10)
            margin-right: functions.toRem(3)
