html
    line-height: 1
    -webkit-text-size-adjust: 100%
    font-size: .83334vw

body
    color: white
    background-color: #212121
    font-size: 16px
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    font-family: 'Roboto', sans-serif
    margin: 0

main
    display: block

h1
    font-size: 2em
    margin: 0.67em 0

hr
    box-sizing: content-box
    height: 0
    overflow: visible

pre
    font-family: monospace, monospace
    font-size: 1em

a
    color: white
    text-decoration: none
    background-color: transparent

abbr[title]
    border-bottom: none
    text-decoration: underline
    text-decoration: underline dotted

b, strong
    font-weight: bold

code, kbd, samp
    font-family: monospace, monospace
    font-size: 1em

small
    font-size: 80%

sub, sup
    font-size: 75%
    line-height: 0
    position: relative
    vertical-align: baseline

sub
    bottom: -0.25em

sup
    top: -0.5em

img
    border-style: none

ul
    margin: 0
    padding: 0

button, input, optgroup, select, textarea
    font-family: inherit
    font-size: 100%
    line-height: 1.15
    margin: 0

button, input
    overflow: visible

button, select
    text-transform: none

button, [type="button"], [type="reset"], [type="submit"]
    -webkit-appearance: button

    &::-moz-focus-inner
        border-style: none
        padding: 0

    &:-moz-focusring
        outline: 1px dotted ButtonText


fieldset
    padding: 0.35em 0.75em 0.625em

legend
    box-sizing: border-box
    color: inherit
    display: table
    max-width: 100%
    padding: 0
    white-space: normal

progress
    vertical-align: baseline

textarea
    overflow: auto

[type="checkbox"], [type="radio"]
    box-sizing: border-box
    padding: 0

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button
    height: auto

[type="search"]
    -webkit-appearance: textfield
    outline-offset: -2px

    &::-webkit-search-decoration
        -webkit-appearance: none


::-webkit-file-upload-button
    -webkit-appearance: button
    font: inherit

details
    display: block

summary
    display: list-item

template
    display: none

[hidden]
    display: none

*
    box-sizing: border-box

@media (max-width: 1000px)
    html
        font-size: 4.26vw
